<template>
   <div id='songlist'  v-if="show">
       <header>
           <span class="iconfont back" @click="back">&#xe79b;</span>
           <img src="https://y.gtimg.cn/music/common/upload/t_playsong_ad/1207759.png?max_age=2592000" alt="">
       </header>
        <div class="bg">
            <img :src="url" alt="">
            <p class="title">{{singername}}</p>
        </div>
        <div class="info">
            <button><span class="iconfont">&#xe60e;</span></button>
        </div>

        <ul class="songList">
            <li class="song-info" v-for="(item,index) in data.songList" :key="index" @click="play(item)">
                <span class="num">{{index+1}}</span>
                <div class="center"> 
                    <p class="song-name">{{item.songName}}</p>
                    <p class="singer">
                        <span v-for="(singer,index) in item.singer" :key="index">
                            {{singer.singerName}} 
                        </span>
                    </p>
                </div>
                <div class="download"><span class="iconfont">&#xe679;</span></div>
            </li>
        </ul>
   </div>
</template>

<script>
   export default {
       name:'',
       data(){
            return{
               val:"",
               data:{},
               url:"",
               singername:"",
               show:true
            }
       },
       created(){
           this.val=this.$route.params.val;
           let strUrl="https://music.niubishanshan.top/api/v2/music/search/"+this.val+"/1/30"
           this.$axios.get(strUrl)
            .then((res)=>{
                this.data=res.data.data;
                this.singername=this.data.songList[0].singer[0].singerName;
                
            })
            .then((res)=>{
                let strUrl2="https://music.niubishanshan.top/api/v2/music/albumImg/"+this.data.songList[0].albumMid+"/"+this.data.songList[0].singer[0].singerMid
                this.$axios.get(strUrl2)
                .then((res)=>{
                    this.url=res.data.data.singerAvatarUrl
                    this.show=true
                })
            })
            .catch(function (error) {
                console.log(error);
            });

       },
       methods:{
           back(){
               this.$router.go(-1)
           },
           play(data){
                 this.$store.state.isplay=true
                var audio= document.getElementById("audio")
                audio.currentTime=0
                var slider= document.getElementById("slider")
                var progress= document.getElementById("progress")
                this.$store.dispatch('GetSongInfo',{
                    albumMid:data.albumMid,
                    singer:data.singer,
                    songId: data.songId,
                    songMid: data.songMid,
                    songName: data.songName
                })
                this.$store.dispatch('GetSongUrl',{
                    albumMid:data.albumMid,
                    singer:data.singer,
                    songId: data.songId,
                    songMid: data.songMid,
                    songName: data.songName
                })

                var progressNum,id,_this=this;
                setTimeout(()=>{
                    frame()
                },500)
                function frame(){
                    var duration=audio.duration
                    var currentTime=audio.currentTime
                    
                    progressNum=currentTime/duration*100
                    if(progressNum<100){
                        progress.style.width=progressNum+"%"
                        id=requestAnimationFrame(frame)
                        _this.$store.commit("change",{progressNum,id})
                    }else{
                        cancelAnimationFrame(id)
                    } 
                }

            }
       }
   }
</script>

<style scoped>
    .songList{
        width: 100%;
        padding-bottom: 50px;
        background: #f8f8f8;
    }
    .songList .total{
       color: grey;
       font-size: 12px;
       padding:10px 10px 0;
    }
    .song-info{
        padding:13px 10px;
        display: flex;
        background: #f8f8f8;
    }
    .songList .song-info .num{
        display: inline-block;
        height: 100%;
        text-align: center;
        color: #f56273;
        font-size: 14px;
        flex: 1;
        line-height: 39px;
        padding-right:10px;
        box-sizing: border-box;
    }
    .songList .song-info .center{
        display: inline-block;
        font-size: 14px;
        flex: 12
    }
    .songList .song-info .center p{
        padding-bottom: 2px
    }
    .singer{
        font-size: 12px;
        color: rgba(0,0,0,.4)
    }
    .songList .song-info .download{
        display: inline-block;
        font-size: 12px;
        color: #b3b3b3;
        flex:1;
        font-size: 18px;
        font-weight: 700;
        line-height: 39px;
    }


    #songlist{
        width: 100%;
        background: #f8f8f8;
        height: 100vh;
    }
    header{
        width: 100%;
        height: 50px;
        background: #f8f8f8;
        position: fixed;
    }
    header img{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 15px;
        width: 30px;
        
    }
    .info{
        text-align: center;
    }
    .back{
        line-height: 50px;
        padding: 15px 10px;
        color: rgba(0, 0, 0, 0.315);
        font-weight: 700;
        font-size: 30px;
        letter-spacing: 2px;
    }
    .bg{
        width: 100%;
        padding: 60px 0 20px;
        text-align: center;

    }
    .bg img{
        width: 50%;
        border-radius: 50%;
         box-shadow: 0 7px 13px rgba(0,0,0,.21);
    }
    .title{
        text-align: center;
        padding: 20px 0;
        font-size: 18px;
    }
    .day{
         text-align: center;
         font-size: 16px
    }
    .updata{
        text-align: center;
        font-size: 14px;
        color: grey;
        padding: 15px;
    }
    button{
        padding: 9px 85px;
        background: #22d59c;
        border: none;
        margin-bottom: 20px;
        border-radius: 50px;
        box-shadow: 0 6px 32px rgba(24,213,156,.5);
    }
    button span{
        color: #fff;
        font-size: 20px;
    }

</style>